#about{
    min-height: 100vh;
    /* display: inline-flex;
    flex-flow: column wrap; */
    /* align-items: center; */

    flex-wrap: wrap;
    background-color: #fefcfb;
    
  
}

.title {
    margin-top: 20px;
    height: 100px;
    color: #fefcfb;
    background-color:  #034078;
    display: flex;
    align-items: center;
    justify-items: center;
    font-size: 20pt;
}

.title h3{
    margin-left: 25pt;
    font-size: 1.5em;
    letter-spacing: 4pt;
  
}

.about-content {
    color: #353535;
    padding-top: 50px;
    margin: 60px 70px 20px 70px;
    flex-wrap: wrap;
    
    display: flex;
  
   
    
    /* justify-content: space-evenly;
    align-items: stretch; */
}

#about-text{
   
    margin-right: 25px;
    max-width: 60%;
    color: #353535;
    font-size: 20px;
}



.about-content li {
    
    font-family: 'Source Code Pro', monospace;
    color: #978ea0;
    text-align: center;
    padding: .75em;
    list-style-type: none;
}

.about-content ul{
   
    column-gap: 20px;
    -moz-column-count: 4;
    -moz-column-gap: 20px;
    -webkit-column-count: 4;
    -webkit-column-gap: 20px;
    column-count: 4;
    /* list-style-type: none; */
}





#prof-pic {
   
    width: 250px;
    height: auto;
    border-radius: 60px;
    /* position: relative; */

    border: 4px solid #284b63;
    padding: 3px;
    
    animation: fadeIn ease 5s;
    -webkit-animation: fadeIn ease 5s;
    -moz-animation: fadeIn ease 5s;
    -o-animation: fadeIn ease 5s;
    -ms-animation: fadeIn ease 5s;

    opacity: 1;
    transition: opacity 500ms ease-in;
      
    
}







.col__2{
    
    /* border-radius: 10px;
    width: 100%; */

    flex-wrap: wrap;

    justify-content: center;
}

